import React from 'react';
import Skill from './Skill';
import { skillTypes } from './consts';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Container from 'react-bootstrap/Container';
import Fade from 'react-reveal/Fade';
const Skills = () => {
  const skillTypesCols = skillTypes.reduce((rows, key, index) => {
    return (index % 4 === 0 ? rows.push([key]) : rows[rows.length - 1].push(key)) && rows;
  }, []);

  return (
    <section id="skills" className="skills section-bg content-box">
      <Container>
        <div className="section-title">
          <h2>Skills</h2>
        </div>
        <Fade top>
          <Row className="pb-4">
            {skillTypesCols.map((col, index) => {
              return (
                <Col lg={6} data-aos="fade-up" key={index}>
                  {col.map((skillName, index) => {
                    return <Skill skill={skillName} key={index} />;
                  })}
                </Col>
              );
            })}
          </Row>
        </Fade>
      </Container>
    </section>
  );
};

export default Skills;
