import React, { useState, useEffect } from 'react';
import ProgressBar from 'react-bootstrap/ProgressBar';
import ProgressLabel from './ProgressLabel';
import styles from './skills.module.css';

const Skill = (props) => {
  const { skill } = props;
  const [skillAmount, setSkillAmount] = useState(0);

  useEffect(() => {
    setSkillAmount(skill.amount);
  }, [skill.amount]);

  return (
    <div className={styles.prograssWrapper}>
      <span className={styles.skillName}>
        {skill.name} <ProgressLabel value={skillAmount} className="val" key={skill.name} />
      </span>
      <div className={styles.customProgressBar}>
        <ProgressBar bsPrefix={styles.progress} now={skillAmount} />
      </div>
    </div>
  );
};
export default Skill;
